age:hover .filler{opacity:0.2;-webkit-filter:blur(5px);filter:blur(5px)}.project-image:hover .caption{opacity:1}.project-image img,.filler,.caption{-webkit-transition:opacity 0.5s ease-out, filter 0.5s ease-out;-webkit-transition:opacity 0.5s ease-out, -webkit-filter 0.5s ease-out;transition:opacity 0.5s ease-out, -webkit-filter 0.5s ease-out;transition:opacity 0.5s ease-out, filter 0.5s ease-out;transition:opacity 0.5s ease-out, filter 0.5s ease-out, -webkit-filter 0.5s ease-out}.caption{position:absolute;color:#000;left:0;top:0;right:0;bottom:0;opacity:0;height:95%}.filler{width:inherit;padding-top:35%;height:166px;background:url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAICAYAAADA+m62AAAAP0lEQVQoU42Q0QoAIAgDz///aMPAKKeQb26HTg0tB6zKVUhI4Bus5tMn2K4Djh7gBGXM7Uvo5riQ/BfUNwwTWfn+Cgg4zkoRAAAAAElFTkSuQmCC) repeat}@media only screen and (max-width: 500px){.caption{font-size:0.8em}}

